body {
  background: #43c6ac;
  background: -webkit-linear-gradient(
    to right,
    #f8ffae,
    #43c6ac
  );
  background: linear-gradient(
    to right,
    #f8ffae,
    #43c6ac
  );
}

.navbar-trans {
  background: transparent;
}

.bg-blur {
    /* Add the blur effect */
    filter: blur(3px);
    -webkit-filter: blur(3px);  
}

.footer {
  height: 60px;
  line-height: 60px; /* Vertically center the text there */
}